import {AUTO_STYLE, AnimationEvent, AnimationPlayer, NoopAnimationPlayer, PRE_STYLE, ÉµAnimationGroupPlayer, ÉµStyleData} from '@angular/animations';

import {AnimationStyleNormalizer} from '../../src/dsl/style_normalization/animation_style_normalizer';
import {AnimationDriver} from '../../src/render/animation_driver';
/**
 * @param {?} players
 * @return {?}
 */
export function optimizeGroupPlayer(players: AnimationPlayer[]): AnimationPlayer {
  switch (players.length) {
    case 0:
      return new NoopAnimationPlayer();
    case 1:
      return players[0];
    default:
      return new ÉµAnimationGroupPlayer(players);
  }
}
/**
 * @param {?} driver
 * @param {?} normalizer
 * @param {?} element
 * @param {?} keyframes
 * @param {?} preStyles
 * @param {?} postStyles
 * @return {?}
 */
export function normalizeKeyframes(
    driver: AnimationDriver, normalizer: AnimationStyleNormalizer, element: any,
    keyframes: ÉµStyleData[], preStyles: ÉµStyleData, postStyles: ÉµStyleData): ÉµStyleData[] {
  const /** @type {?} */ errors: string[] = [];
  const /** @type {?} */ normalizedKeyframes: ÉµStyleData[] = [];
  let /** @type {?} */ previousOffset = -1;
  let /** @type {?} */ previousKeyframe: ÉµStyleData = null;
  keyframes.forEach(kf => {
    const /** @type {?} */ offset = /** @type {?} */(( kf['offset'] as number));
    const /** @type {?} */ isSameOffset = offset == previousOffset;
    const /** @type {?} */ normalizedKeyframe: ÉµStyleData = isSameOffset ? previousKeyframe : {};
    Object.keys(kf).forEach(prop => {
      let /** @type {?} */ normalizedProp = prop;
      let /** @type {?} */ normalizedValue = kf[prop];
      if (normalizedValue == PRE_STYLE) {
        normalizedValue = preStyles[prop];
      } else if (normalizedValue == AUTO_STYLE) {
        normalizedValue = postStyles[prop];
      } else if (prop != 'offset') {
        normalizedProp = normalizer.normalizePropertyName(prop, errors);
        normalizedValue = normalizer.normalizeStyleValue(prop, normalizedProp, kf[prop], errors);
      }
      normalizedKeyframe[normalizedProp] = normalizedValue;
    });
    if (!isSameOffset) {
      normalizedKeyframes.push(normalizedKeyframe);
    }
    previousKeyframe = normalizedKeyframe;
    previousOffset = offset;
  });
  if (errors.length) {
    const /** @type {?} */ LINE_START = '\n - ';
    throw new Error(
        `Unable to animate due to the following errors:${LINE_START}${errors.join(LINE_START)}`);
  }

  return normalizedKeyframes;
}
/**
 * @param {?} player
 * @param {?} eventName
 * @param {?} event
 * @param {?} callback
 * @return {?}
 */
export function listenOnPlayer(
    player: AnimationPlayer, eventName: string, event: AnimationEvent,
    callback: (event: any) => any) {
  switch (eventName) {
    case 'start':
      player.onStart(
          () => { callback(event && copyAnimationEvent(event, 'start', player.totalTime)); });
      break;
    case 'done':
      player.onDone(
          () => { callback(event && copyAnimationEvent(event, 'done', player.totalTime)); });
      break;
    case 'destroy':
      player.onDestroy(
          () => { callback(event && copyAnimationEvent(event, 'destroy', player.totalTime)); });
      break;
  }
}
/**
 * @param {?} e
 * @param {?=} phaseName
 * @param {?=} totalTime
 * @return {?}
 */
export function copyAnimationEvent(
    e: AnimationEvent, phaseName?: string, totalTime?: number): AnimationEvent {
  return makeAnimationEvent(
      e.element, e.triggerName, e.fromState, e.toState, phaseName || e.phaseName,
      totalTime == undefined ? e.totalTime : totalTime);
}
/**
 * @param {?} element
 * @param {?} triggerName
 * @param {?} fromState
 * @param {?} toState
 * @param {?=} phaseName
 * @param {?=} totalTime
 * @return {?}
 */
export function makeAnimationEvent(
    element: any, triggerName: string, fromState: string, toState: string, phaseName: string = null,
    totalTime: number = null): AnimationEvent {
  return {element, triggerName, fromState, toState, phaseName, totalTime};
}
/**
 * @param {?} map
 * @param {?} key
 * @param {?} defaultValue
 * @return {?}
 */
export function getOrSetAsInMap(
    map: Map<any, any>| {[key: string]: any}, key: any, defaultValue: any) {
  let /** @type {?} */ value: any;
  if (map instanceof Map) {
    value = map.get(key);
    if (!value) {
      map.set(key, value = defaultValue);
    }
  } else {
    value = map[key];
    if (!value) {
      value = map[key] = defaultValue;
    }
  }
  return value;
}
